<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <!-- TODO:  -->
            <el-row :gutter="20">
                <el-col :span="8"
                    ><el-input
                        placeholder=""
                        v-model="queryInfo.query"
                        clearable
                        @clear="getOrdersList"
                    >
                        <el-button
                            slot="append"
                            icon="el-icon-search"
                            @click="getOrdersList"
                        ></el-button> </el-input
                ></el-col>
            </el-row>
            <!--  -->
            <el-table
                :data="ordersList"
                stripe
                border
                style="width: 100%"
                height="450"
            >
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column
                    prop="order_number"
                    label=""
                ></el-table-column>
                <el-table-column
                    prop="order_price"
                    label=""
                    width="110px"
                ></el-table-column>
                <el-table-column label="" width="120px">
                    <template v-slot="scope">
                        <el-tag
                            type="danger"
                            v-if="scope.row.pay_status === '0'"
                            ></el-tag
                        >
                        <el-tag type="success" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="is_send"
                    label=""
                    width="120px"
                ></el-table-column>
                <el-table-column label="" width="120px">
                    <template v-slot="scope">
                        {{ scope.row.create_time | dataFormat }}
                    </template>
                </el-table-column>

                <el-table-column label="" width="150px">
                    <template v-slot="">
                        <el-tooltip
                            class="item"
                            effect="dark"
                            content=""
                            placement="top"
                        >
                            <el-button
                                type="primary"
                                icon="el-icon-edit"
                                circle
                                @click="showBox"
                            ></el-button>
                        </el-tooltip>
                        <el-tooltip
                            class="item"
                            effect="dark"
                            content=""
                            placement="top"
                        >
                            <el-button
                                type="success"
                                icon="el-icon-location"
                                circle
                                @click="showProgressBox"
                            ></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[7, 10, 15, 25]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </el-card>
        <!--  -->
        <edit-addr
            :addressDialogVisible="addressDialogVisible"
            @changeEditAddressDialogVisible="changeEditAddressDialogVisible"
        ></edit-addr>
        <!--    -->
        <el-dialog
            title=""
            :visible.sync="progressDialogVisible"
            width="50%"
        >
            <el-timeline :reverse="false">
                <el-timeline-item
                    v-for="(activity, index) in progressInfo"
                    :key="index"
                    :timestamp="activity.ftime"
                >
                    {{ activity.context }}
                </el-timeline-item>
            </el-timeline>
        </el-dialog>
    </div>
</template>

<script>
import EditAddr from './EditAddr/index.vue'
export default {
    components: { EditAddr },
    data() {
        return {
            // 
            queryInfo: {
                // 
                query: '',
                // 
                pagenum: 1,
                // 
                pagesize: 10,
            },
            // 
            total: 0,
            // 
            ordersList: [],
            // 
            addressDialogVisible: false,
            // 
            progressDialogVisible: false,
            progressInfo: [],
        }
    },
    created() {
        this.getOrdersList()
    },
    methods: {
        // 
        async getOrdersList() {
            console.log(this.queryInfo)
            const { data: res } = await this.$http.get('orders', {
                params: this.queryInfo,
            })
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.$message.success('!')
            console.log(res.data.goods)
            this.ordersList = res.data.goods
            this.total = res.data.total
        },
        //  pagesize  
        handleSizeChange(val) {
            this.queryInfo.pagesize = val
            this.getOrdersList()
        },
        //    
        handleCurrentChange(val) {
            this.queryInfo.pagenum = val
            this.getOrdersList()
        },
        // 
        showBox() {
            // TODO: 
            this.addressDialogVisible = true
        },
        // ()
        changeEditAddressDialogVisible(val) {
            this.addressDialogVisible = val
        },
        // 
        async showProgressBox() {
            const { data: res } = await this.$http.get('/kuaidi/1106975712662')
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.progressInfo = res.data
            this.progressDialogVisible = true
        },
    },
}
</script>

<style>
</style>