export const userLoginFormRulesMixin = {
    data() {
        return {
            loginFormRules: {
                // 
                username: [{
                        required: true,
                        message: '',
                        trigger: 'blur',
                    },
                    {
                        min: 3,
                        max: 10,
                        message: ' 3  10 ',
                        trigger: 'blur',
                    },
                ],
                // 
                password: [{
                        required: true,
                        message: '',
                        trigger: 'blur',
                    },
                    {
                        min: 6,
                        max: 15,
                        message: ' 6  15 ',
                        trigger: 'blur',
                    },
                ],
            },

        }
    }
}
export const userFormRules = {
    data() {
        // 
        var checkEmail = (rule, value, callback) => {
            // 
            const regEmail = /^\w+@\w+(\.\w+)+$/
            if (regEmail.test(value)) {
                return callback() // 
            }
            // 
            callback(new Error(''))
        }
        // 
        var checkMobeli = (rule, value, callback) => {
            const regMobile = /^1[34578]\d{9}$/
            if (regMobile.test(value)) {
                return callback()
            }
            // 
            callback(new Error(''))
        }
        return {
            // 
            addFormRules: {
                username: [{
                        required: true,
                        message: '',
                        trigger: 'blur',
                    },
                    {
                        min: 3,
                        max: 10,
                        message: '3~10',
                        trigger: 'blur',
                    },
                ],
                password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 6,
                        max: 15,
                        message: ' 6  15 ',
                        trigger: 'blur'
                    }
                ],
                email: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        validator: checkEmail,
                        trigger: 'blur'
                    }
                ],
                mobile: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        validator: checkMobeli,
                        trigger: 'blur'
                    }
                ]
            },
            // 
            editFormRules: {
                email: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        validator: checkEmail,
                        trigger: 'blur'
                    }
                ],
                mobile: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        validator: checkMobeli,
                        trigger: 'blur'
                    }
                ]
            }
        }
    }
}
