<template>
    <!--    -->
    <el-dialog
        title=""
        :visible.sync="addressDialogVisible"
        @close="addressDialogClosed"
        :before-close="handleAddClose"
        width="50%"
    >
        <el-form
            :model="addressForm"
            :rules="addressFormRules"
            ref="addressFormRef"
            label-width="100px"
        >
            <el-form-item label="/" prop="address1">
                <!--          <el-input v-model="addressForm.address1"></el-input>-->
                <el-cascader
                    :options="cityData"
                    v-model="addressForm.address1"
                    :props="{ expandTrigger: 'hover' }"
                ></el-cascader>
            </el-form-item>
            <el-form-item label="" prop="address2">
                <el-input v-model="addressForm.address2"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="changeEditAddressDialogVisible"> </el-button>
            <el-button type="primary" @click="changeEditAddressDialogVisible"
                > </el-button
            >
        </span>
    </el-dialog>
</template>

<script>
import cityData from './citydata'
import { editOrdersFormRulesMinix } from '@/common/mixin.js'
export default {
    mixins: [editOrdersFormRulesMinix],
    data() {
        return {
            addressForm: {
                address1: [],
                address2: '',
            },
            // 
            cityData,
        }
    },
    props: {
        addressDialogVisible: Boolean,
    },

    methods: {
        // 
        addressDialogClosed() {
            this.$refs.addressFormRef.resetFields()
        },
        //  Dialog 
        handleAddClose() {
            this.$confirm('')
                .then((_) => {
                    this.changeEditAddressDialogVisible()
                })
                .catch((_) => {})
        },
        changeEditAddressDialogVisible() {
            this.$emit('changeEditAddressDialogVisible', false)
        },
    },
}
</script>

